import React, { createContext, useContext, ReactNode } from 'react';
import {
  CustomComponentProps,
  EntityPageMoreActionsActionParams,
  IColumnValue,
  SchemaConfig,
} from '../types';

import { CustomActionParams, ResolvedAction } from '../utils/actions/types';
import {
  CollectionPageActionOnRowClickParams,
  CollectionPageActionParams,
} from '../types/actions/collectionPageActions';
import { ActionCellActionParams } from '../types/actions/actionCell';

export interface PatternsWizardOverridesContextProps {
  actions?: Record<
    string,
    (
      props: CustomActionParams<
        | EntityPageMoreActionsActionParams
        | CollectionPageActionParams
        | ActionCellActionParams
        | CollectionPageActionOnRowClickParams
      >,
    ) => ResolvedAction
  >;
  columns?: Record<string, (props: IColumnValue<any>) => void>;
  modals?: Record<string, React.FC<any>>;
  HeaderAddon?: React.FC;
  emptyState?: Record<string, () => void>;
  components?: Record<string, React.FC<CustomComponentProps>>;
  customDataSources?: Record<
    string,
    (collectionId: string, context: any) => Promise<SchemaConfig>
  >;
}

const PatternsWizardOverridesContext = createContext<
  PatternsWizardOverridesContextProps | undefined
>(undefined);

export const PatternsWizardOverridesProvider: React.FC<{
  children: ReactNode;
  value: PatternsWizardOverridesContextProps;
}> = ({ children, value }) => {
  return (
    <PatternsWizardOverridesContext.Provider value={value}>
      {children}
    </PatternsWizardOverridesContext.Provider>
  );
};

export const usePatternsWizardOverridesContext = ():
  | PatternsWizardOverridesContextProps
  | undefined => {
  return useContext(PatternsWizardOverridesContext);
};
